.main-header__toggle {
  position: absolute;
  top: 37px;
  right: 12px;
  z-index: 2;
  display: block;
  width: 57px;
  height: 55px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  display: none;
}

.main-header__nav--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  width: 33px;
  height: 4px;
  background-color: #ff4000;
  box-shadow: 0 9px 0 0 #ff4000, 0 18px 0 0 #ff4000;
}

.main-header__nav--opened .main-header__toggle {
  top: 27px;
  right: 0;
  left: auto;
  z-index: 11;
}
.main-header__nav--opened .main-header__toggle::before, .main-header__nav--opened .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 37px;
  right: 19px;
  width: 33px;
  height: 3px;
  background-color: #000;
}
.main-header__nav--opened .main-header__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-header__nav--opened .main-header__toggle::after {
  transform: rotate(-45deg);
}

@media screen and (max-width: 548px) {
  .main-header__nav--opened {
    position: relative;
    width: 100%;
    max-width: 100%;
    z-index: 1;
